import styled from 'styled-components';

export const L = styled.svg`
  background: url('${require('assets/logo.svg')}') no-repeat;
  width: 100px;
  height: 100px;
  background-size: 100%;
`;

export const LogoDiv = styled.div`
  height: 100px;
  width: 100px;
`;



