import {
  injectGlobal
} from 'styled-components';

/* eslint-disable */
injectGlobal `
  @import url('https://fonts.googleapis.com/css?family=VT323');

  body {
    font-family: 'museo-sans', 'sans-serif';
  }

  ${''/* div {
    border:1px black dashed;
  } */}
`



